body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}



body {
    background-color:#c9c9c9;
    display:-webkit-flex;
    display:flex;
    -webkit-justify-content:center;
            justify-content:center;
}
  
button {
    background-color: #4CAF50; /* Green */
    border: 1px solid #417f44;
    border-radius:3px;
    box-shadow: 0 2px 6px 0 rgba(0,0,0,0.2), 0 6px  20px 0 rgba(0,0,0,0.19);
    color: white;
    cursor:pointer;
    text-align: center;
    text-decoration: none;
    font-size: 20px;
}

.buttonPressed {
    background-color: rgb(112, 214, 115);
}

#buttons {
      display:grid;
      -webkit-flex-grow: 2;
              flex-grow: 2;
      grid-gap: 5px;
      grid-template-columns: 1fr 1fr 1fr 1fr;
      grid-template-rows: 1fr 1fr 1fr 1fr 1fr;
      width: 100%;
      
}

button:focus {
    outline: 0;
}

#buttons > #btn0 {
    grid-column-start:1;
    grid-column-end: 3;
}

#buttons > #AC, #CE {
    background-color: rgb(173, 0, 0);
    border: 1px solid rgb(173, 0, 0);
}

#buttons > #AC:active, #CE:active {
    background-color: rgb(194, 50, 50);
}

#buttons > #equals, #invertSign {
    background-color: rgb(231, 181, 73);
    border: 1px solid rgb(231, 181, 73);
}

#buttons > #equals:active, #invertSign:active {
    background-color: rgb(238, 196, 105);
}

#calculator {
    background-color:#484c51;
    border-radius:10px;
    box-shadow: 1px 1px 10px 2px #222121, inset -1px -3px 10px 1px #515151;
    margin-top:20px;
    padding:20px 20px 20px 20px;
    width: 275px;
    height: 375px;
    /*Flexbox Additions*/
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction:row;
            flex-direction:row;
    -webkit-flex-wrap:wrap;
            flex-wrap:wrap;
    -webkit-justify-content: center;
            justify-content: center;
}
  
#display {
    background-color: #cec9be;
    border: 3px solid #5f5a50;
    border-radius:5px;
    -webkit-flex-grow: 1;
            flex-grow: 1;
    margin-bottom:20px;
    padding:0px 6px 0px 0px;
    text-align:right;
    max-width: 95%;
}
  
#displayText {
    color:#5a5a4c;
    font-family: 'Orbitron', sans-serif;
